@font-face {
  font-family: "Robot";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Robot Regular"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
}
@font-face {
  font-family: "Robot";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Robot Medium"), url("../fonts/Roboto-Medium.woff") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
}
@font-face {
  font-family: "Robot";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Robot Bold"), url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.list {
  font-family: "Robot", Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin: 0 auto;
  min-width: 1200px;
  color: #000000;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 50px 100px;
}
.list__item {
  width: 240px;
  height: 250px;
  padding: 25px;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  background-color: #61c98b;
  color: #FFFFFF;
}
.list__wrap {
  display: flex;
  justify-content: space-between;
}
.list__image-holder {
  height: 65px;
  width: 65px;
  border-radius: 50%;
  background-color: rgba(88, 165, 119, 0.6);
}
.list__title {
  font-size: 30px;
  font-weight: normal;
  margin: 20px 0;
}
.list__item:nth-child(2) {
  background: #FFFFFF;
  color: #6B6B6C;
}
.list__item:nth-child(2) .list__image-holder {
  background-color: #FFFFFF;
}
.holder__title {
  font-family: "Robot", "Arial", sans-serif;
  font-size: 20px;
  line-height: 22px;
  font-weight: bold;
  text-align: right;
}
.holder__list {
  list-style: none;
  padding: 0;
  text-align: right;
  text-transform: capitalize;
  font-size: 16px;
}
.holder__text {
  text-align: right;
  margin: 0;
}
.holder__text span {
  text-transform: uppercase;
}
